import React from "react";
import emailjs from "emailjs-com";
import "../../App.css";

import style from "../css/Footer.module.css";

function Footer() {
  function sendEmail(e) {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_b5p3fbc",
        "template_j4b7nsj",
        e.target,
        "user_9TIMzOzG3X3lMgfqbVmzI"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
  }
  return (
    <div className={style.wrapper}>
      <section id="contact" className={style.contact}>
        <div className={style.skills_info}>
          <h1 className={style.skillshead}>Contact me</h1>

          <div className={style.contact_ftom_contaner}>
            <form onSubmit={sendEmail}>
              <input
                type="hidden"
                name="subject"
                value="Contact For Some dtails about work "
              />
              <input
                name="name"
                type="text"
                className={style.fullname}
                placeholder="full name"
              />
              <input
                name="email"
                type="email"
                className={style.Email}
                placeholder="Example@E-mail.com"
              />
              <textarea
                name="message"
                rows="17"
                cols="50"
                className={style.contact_form_message}
                placeholder="Your message"
              ></textarea>
              <input type="submit" value="Send Email" />
            </form>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;
